.flexBox{
    display: flex;
    border-bottom: 1px solid black;
    padding-left: 5px;
    padding-top: 5px;
}

.href{
    color: #777;
    text-decoration: none;
    margin: 0 10px 10px 10px;
    padding: 2px;
    transition: .5s;
}

.href:hover{
    color: black;
    background-color: #fff;
}

.lastFlexBox{
    border: none;
}